/*--------------popup car-type--------------*/

.route-payment .left-popup-menu{
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.65);
}
.route-payment .left-popup-menu.car-type-popup{
    display: none;
}

.route-payment .left-popup-menu-content{
    width: 80%;
    height: 100%;
    min-height: 690px;
    background-color: #fff;
    position: absolute;
    top:0;
    right: 0;
}
@media screen and (max-width: 650px) {
    .route-payment .left-popup-menu-content{
        width: 90%;
    }
}
.route-payment .popup-title{
    font-size: 32px;
    color: #abadb6;
    font-weight: 600;
    margin-top: 63px;
    padding-left: 9%;
}
@media screen and (max-width: 510px) {
    .route-payment .popup-title{
        font-size: 19px;
        margin-top: 20px;
    }
}

.route-payment .left-popup-menu label{
    display: block;
    width: 85%;
    border: solid 2px #dbdde7;
    background-color: #fff;
    border-radius: 9px;
    margin: 0 auto 20px;
    padding: 14px 10px 0 32% ;
    font-size: 30px;
    color: #babcc8;
    font-weight: normal;
}
@media screen and (max-width: 650px) {
    .route-payment .left-popup-menu label{
        width: 92%;
        padding-left: 45%;
        padding-right: 0;
    }
}
@media screen and (max-width: 380px) {
    .left-popup-menu label{
        padding-left: 53%;
        font-size: 21px;
    }
}

.route-payment .left-popup-menu label p{
    font-size: 24px;
    color: #abadb6;
    margin-top: 5px;
}
@media screen and (max-width: 650px) {
    .route-payment .left-popup-menu label p{
        font-size: 16px;
    }
}
.route-payment .left-popup-menu input{
    display: none;
}


.route-payment .left-popup-menu input#economic + label{
    background: url("../../../assets/img/car_class_economic.png") no-repeat left 6% top 50%;
    background-color: #fff;
}
.route-payment .left-popup-menu input#comfort + label{
    background: url("../../../assets/img/car_class_comfort.png") no-repeat left 6% top 50%;
    background-color: #fff;
}
.route-payment .left-popup-menu input#van + label{
    background: url("../../../assets/img/car_class_van.png") no-repeat left 6% top 50%;
    background-color: #fff;
}
.route-payment .left-popup-menu input#economic:checked + label{
    background: url("../../../assets/img/car_class_economic.png") no-repeat left 6% top 50%, url("../../../assets/img/car_class_mark.png") no-repeat right 3% top 13%;
    border: solid 2px #7664ca;
    color: #7664ca;
}
.route-payment .left-popup-menu input#comfort:checked + label{
    background: url("../../../assets/img/car_class_comfort.png") no-repeat left 6% top 50%, url("../../../assets/img/car_class_mark.png") no-repeat right 3% top 13%;
    border: solid 2px #7664ca;
    color: #7664ca;
}
.route-payment .left-popup-menu input#van:checked + label{
    background: url("../../../assets/img/car_class_van.png") no-repeat left 6% top 50%, url("../../../assets/img/car_class_mark.png") no-repeat right 3% top 13%;
    border: solid 2px #7664ca;
    color: #7664ca;
}
@media screen and (max-width: 430px) {
    .route-payment .left-popup-menu input#economic:checked + label{
        background: url("../../../assets/img/car_class_mark.png") no-repeat left 5% top 13%, url("../../../assets/img/car_class_economic.png") no-repeat left 6% top 50%;
        color: #7664ca;
    }
    .route-payment .left-popup-menu input#comfort:checked + label{
        background: url("../../../assets/img/car_class_mark.png") no-repeat left 5% top 13%, url("../../../assets/img/car_class_comfort.png") no-repeat left 6% top 50%;
        color: #7664ca;
    }
    .route-payment .left-popup-menu input#van:checked + label{
        background: url("../../../assets/img/car_class_mark.png") no-repeat left 5% top 13%, url("../../../assets/img/car_class_van.png") no-repeat left 6% top 50%;
        color: #7664ca;
    }
}

.route-payment .absolutely-positioned{
    position: absolute;
    bottom: 43px;
    width: 100%;
}



