
.route-payment.container{
    display: block;
    width: 100%;
}

.route-payment-content{
    padding-top: 20px;
    padding-bottom: 280px;
    height: calc(100vh - 231px);
    overflow: scroll;

}
@media screen and (max-height: 630px){
    .route-payment-content{
        padding-bottom: 0px;
        overflow: visible;
    }
}
@media screen and (max-width: 540px){
    .route-payment-content{
        padding-bottom: 210px;
    }
}
.route-payment .content-data-button-wrapper{
    font-size: 0;
    vertical-align: top;
    position: relative;
}

.route-payment .content-data-button-wrapper .badge{
    width: 118px;
    height: 117px;
    display: inline-block;
}
@media screen and (max-width: 580px){
    .route-payment .content-data-button-wrapper .badge {
        width: 93px;
        height: 93px;
    }
}

.route-payment .content-data-button-wrapper .badge-a{
    background: url("../../../assets/img/route-a-bgr.png") no-repeat top 50% left 50%;
}
.route-payment .content-data-button-wrapper-wrapper .badge-b{
    background: url("../../../assets/img/route-b-bgr.png") no-repeat top 50% left 50%;
}
.route-payment .content-data-button-wrapper .pen{
    background: url("../../../assets/img/pen.png") no-repeat top 50% left 50%;
}
.route-payment .content-data-button-wrapper .card{
    background: url("../../../assets/img/card.png") no-repeat top 50% left 50%;
}

.route-payment .number-of-route{
    display: block;
    width: 62px;
    height: 62px;
    background: url("../../../assets/img/plus-route.png") no-repeat top 50% left 50%;
    position: absolute;
    bottom: -28px;
    right: 85px;
    border-radius: 50%;
    -webkit-box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.15);
            box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.15);;
}
@media screen and (max-width: 580px){
    .route-payment .number-of-route{
        width: 50px;
        height: 50px;
        right: 42px;
        bottom: -19px;
    }
}

.route-payment .number-of-route.close{
    background: url("../../../assets/img/plus-route-close.png") no-repeat top 50% left 50%;
}

.route-payment .additional-buttons{
    margin: 38px auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 92%;
}
.route-payment .shadowed-button{
    width: 47%;
    height: 132px;
    background-color: #fff;
    -webkit-box-shadow: 5px 5px 12px 2px #d5d4d4;
            box-shadow: 5px 5px 12px 2px #d5d4d4;
    border-radius: 7px;
}
.route-payment .shadowed-button-title{
    font-weight: normal;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #abadb6;
    margin-bottom: 8px;
    font-weight: 600;
}
.route-payment .shadowed-button-text{
    font-size: 32px;
    color: #9c64e2;
}
@media screen and (max-width: 580px){
    .route-payment .shadowed-button{
        height: 90px;
        background-color: #fff;
        -webkit-box-shadow: 5px 5px 12px 2px #d5d4d4;
                box-shadow: 5px 5px 12px 2px #d5d4d4;
        border-radius: 7px;
    }
    .route-payment .shadowed-button-title{
        font-size: 16px;
        font-weight: normal;
        letter-spacing: 0px;
        text-transform: lowercase;
        margin-bottom: 2px;
    }
    .route-payment .shadowed-button-text{
        font-size: 22px;
    }
}

.route-payment footer{
    width: 100%;
    border-top: solid 1px #e7e8ef;
    position: absolute;
    bottom: 0;
    background-color: #fff;
    /*height: 390px;*/
    -webkit-box-shadow: 0px -5px 10px #e7e8ef;
            box-shadow: 0px -5px 10px #e7e8ef;
}
@media screen and (max-height: 630px){
    .route-payment footer{
        overflow: static;
    }
}

.route-payment .footer-text{
    text-align: center;
    color: #abadb6;
    font-weight: 600;
    padding: 40px 0 14px 0;
}
.route-payment .footer-text.second{
    padding: 0px;
}
.route-payment .price-container{
    margin: 35px auto 49px;
    width: 87.5%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    color: #292b3a;
    font-size: 52px;
}
.route-payment .price-container button{
    width: 29%;
    min-width: 86px;
    height: 60px;
    border-radius: 43px;
    font-size: 28px;
}
.route-payment .price-container .button-minus{
    background-color: #f9e3e8;
    color: #da4665;
}
.route-payment .price-container .button-plus{
    background-color: #e2faec;
    color: #3ddc80;
}
@media screen and (max-width: 540px){
    .route-payment .calculated-price{
        font-size: 30px;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
        padding-bottom: 10px;
    }
    .route-payment .price-container{
        margin: 15px auto 15px ;
    }
    .route-payment .price-container button{
        font-size: 22px;
    }
    .route-payment footer{
        height: 240px;
    }
    .route-payment .footer-text{
        padding-top: 15px;
    }
}




